import { autoEffect, store } from "@risingstack/react-easy-state";
import { makeBox } from "../lib/box";

export const props = store({
  width: 100,
  height: 200,
  depth: 600,
  finger: 20,
  thickness: 10,
});

export const result = store({
  sides: {},
});

autoEffect(() => {
  const sides = makeBox(props);
  Object.entries(sides).forEach(([name, side]) => (result.sides[name] = side));
});
