const arr2vec = (arr) => ({ x: arr[0], y: arr[1] });
const vec2arr = (vec) => [vec.x, vec.y];
const Vector = {
  center: (v1, v2) => ({ x: (v1.x + v2.x) / 2, y: (v1.y + v2.y) / 2 }),
  scale: (v1, s) => ({ x: v1.x * s, y: v1.y * s }),
  add: (v1, v2) => ({ x: v1.x + v2.x, y: v1.y + v2.y }),
  sub: (v1, v2) => ({ x: v1.x - v2.x, y: v1.y - v2.y }),
  dot: (v1, v2) => ({ x: v1.x * v2.y, y: v1.x * v2.y }),
  travel: (from, to, s) =>
    Vector.add(from, Vector.scale(Vector.sub(to, from), s)),
  dist: (v1, v2) =>
    Math.sqrt(Math.pow(v2.x - v1.x, 2) + Math.pow(v2.y - v1.y, 2)),
  length: (v1) => Math.sqrt(v1.x * v1.x + v1.y * v1.y),
  rotate90: (v1) => ({ x: -v1.y, y: v1.x }),
  rotate90m: (v1) => ({ x: v1.y, y: -v1.x }),
  norm: (v1) => Vector.scale(v1, 1 / Vector.length(v1)),
};

export function makeDado(from, to) {}

export function makeFinger(from, to, pos, width, direction, thickness) {
  let dir = Vector.norm(Vector.sub(to, from));
  if (direction === "out") {
    dir = Vector.scale(dir, -1);
  }

  let dist = Vector.dist(from, to);

  if (dist === 0) {
    dist = 100.0;
  }

  const start = Vector.travel(to, from, pos + width / dist);
  const end = Vector.travel(to, from, pos - width / dist);
  const p0 = Vector.add(start, Vector.scale(Vector.rotate90(dir), thickness));
  const p1 = Vector.add(end, Vector.scale(Vector.rotate90(dir), thickness));

  return [vec2arr(start), vec2arr(p0), vec2arr(p1), vec2arr(end)];
}

export function makeEdge(sort, fromArr, toArr, { side, finger, thickness }) {
  const from = arr2vec(fromArr);
  const to = arr2vec(toArr);

  const position = 0.5;
  const width = finger;

  const isInside = () =>
    ["right", "left"].includes(side) && (sort === 0 || sort === 2);
  const isTopBottom = () => ["top", "bottom"].includes(side);

  const direction = isTopBottom() || isInside() ? "in" : "out";

  return [
    vec2arr(from),
    ...makeFinger(from, to, position, width, direction, thickness),
    vec2arr(to),
  ];
}

export function makeSide(name, x, y, ctx) {
  const edgeCtx = { side: name, ...ctx };
  return [
    ...makeEdge(0, [0, 0], [x, 0], edgeCtx),
    ...makeEdge(1, [x, 0], [x, y], edgeCtx),
    ...makeEdge(2, [x, y], [0, y], edgeCtx),
    ...makeEdge(3, [0, y], [0, 0], edgeCtx),
  ];
}

export function makeBox({ width, height, depth, finger = 20, thickness = 10 }) {
  console.log("makeBox", { width, height, depth, finger, thickness });
  const ctx = { width, height, depth, finger, thickness };

  return {
    top: makeSide("top", height, width, ctx),
    bottom: makeSide("bottom", height, width, ctx),
    front: makeSide("front", height, depth, ctx),
    back: makeSide("back", height, depth, ctx),
    left: makeSide("left", depth, width, ctx),
    right: makeSide("right", depth, width, ctx),
  };
}
